.team {
    .block-team {
        .__image {
            margin-bottom : 32px;
            > div {
                overflow: hidden;
            }

            img {
                width: 100%;
                transform: scale(1);
                .easing-animation();
            }
        }

        .__info {
            text-align: center;
            .__name{
                margin-bottom : 5px;
            }
            .__role{
                margin-bottom : 10px;
            }
            .__text{}
            .social{}
        }

        &:hover {
            .__image {
                img {
                    transform: scale(1.08);
                }
            }
        }
    }

    @media @sm {
        &.team-horizontal {
            .block-team {
                .clearfix;
                margin-right: -15px;
                margin-left: -15px;

                > div {
                    width: 50%;
                    float: left;
                    padding-left : 15px;
                    padding-right : 15px;
                }

                .__image {
                    margin-bottom : 0px;
                    img {
                        width: 100%;
                    }
                }

                .__info {
                    text-align: left;
                    .__name{
                        margin-bottom : 5px;
                    }
                    .__role{
                        margin-bottom : 10px;
                    }
                    .__text{}
                    .social{}
                }
            }
        }
    }
}