/* Overlay */
/* Overlay static */
.overlay-container {
  position: relative;
}
.overlay {
  transition: all 0.3s ease-in-out;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 512;
}
/* Overlay hover */
.overlay-hover {
  transition: all 0.3s ease-in-out;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 512;
  opacity: 0;
}
.overlay-container .overlay-hover:hover {
  opacity: 1;
}
