/* Background */
.parallax-wrapper {
  position: relative;
  overflow: hidden;
}
.parallax-background {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -2;
  background-size: cover;
  background-repeat: repeat-y;
  background-position-x: center;
}
html.chrome:not(.parallax-on) .parallax-background {
  height: 120%;
  top: -10%;
}
html:not(.parallax-on) .parallax-background {
  background-attachment: fixed;
}
.parallax-background .parallax-minor {
  transition: all 0s;
}
.backgroud-wrapper {
  position: relative;
}
.static-background {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -2;
  background-size: cover;
