/* Modal */
.jquery-modal.blocker {
  transform: translate3d(0px, 0px, 0px);
}
.modal {
  display: none;
  position: absolute;
  background: #FFFFFF;
  padding: 30px;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
}
.modal a.close-modal {
  display: block;
  position: absolute;
  background-color: #FFBF00;
  color: #FFFFFF;
  top: 0;
  right: 0;
  font-size: 0;
}
.modal a.close-modal:before {
  font-family: "megatron" !important;
  font-size: 18px;
  line-height: 30px;
  width: 30px;
  text-align: center;
  display: block;
  float: left;
  content: "\e07c";
}
.modal-spinner {
  display: none;
  width: 64px;
  height: 64px;
  position: fixed;
  background-color: #FFFFFF;
  border: 1px solid #DDDDDD;
  border-radius: 50%;
  color: #FFBF00;
  line-height: 64px;
  font-size: 38px;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-right: -32px;
  margin-top: -32px;
}
.modal-video-inner {
  width: 960px;
  position: relative;
  max-width: 100%;
}
.modal .search-box-large {
  width: 100vh;
  max-width: 800px;
  padding: 30px;
}
/* Dropdown */
.dropup,
.dropdown {
  position: relative;
}
.dropdown-toggle:focus {
  outline: 0;
}
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
}
.dropdown-menu.pull-right {
  right: 0;
  left: auto;
}
.open > .dropdown-menu {
  display: block;
}
.dropdown-menu-right {
  left: auto;
  right: 0;
}
.dropdown-menu-left {
  left: 0;
  right: auto;
}
.dropdown-backdrop {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 990;
}
.pull-right .dropdown-menu {
  right: 0;
  left: auto;
}
