/* Magnific Popup */
/* Magnific Popup CSS */
.mfp-bg {
  background: #0b0b0b;
  height: 100%;
  left: 0;
  opacity: 0.8;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 11002;
}
.mfp-fade.mfp-bg {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8;
}
/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0;
}
/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
}
/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
}
.mfp-wrap {
  -webkit-backface-visibility: hidden;
  height: 100%;
  left: 0;
  outline: none !important;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 11003;
}
.mfp-container {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 100%;
  left: 0;
  padding: 0 8p;
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%;
}
.mfp-container:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.mfp-align-top .mfp-container:before {
  display: none;
}
.mfp-content {
  display: inline-block;
  margin: 0 auto;
  position: relative;
  text-align: left;
  vertical-align: middle;
  z-index: 11005;
}
.mfp-inline-holder .mfp-content,
.mfp-ajax-holder .mfp-content {
  cursor: auto;
  width: 100%;
}
.mfp-ajax-cur {
  cursor: progress;
}
.mfp-zoom-out-cur,
.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: -moz-zoom-out;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}
.mfp-zoom {
  cursor: pointer;
  cursor: -webkit-zoom-in;
  cursor: -moz-zoom-in;
  cursor: zoom-in;
}
.mfp-auto-cursor .mfp-content {
  cursor: auto;
}
.mfp-close,
.mfp-arrow,
.mfp-preloader,
.mfp-counter {
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
.mfp-loading.mfp-figure {
  display: none;
}
.mfp-preloader {
  color: #ccc;
  left: 8px;
  margin-top: -0.8em;
  position: absolute;
  right: 8px;
  text-align: center;
  top: 50%;
  width: auto;
  z-index: 11004;
}
.mfp-preloader a {
  color: #ccc;
}
.mfp-preloader a:hover {
  color: #fff;
}
.mfp-s-ready .mfp-preloader {
  display: none;
}
.mfp-s-error .mfp-content {
  display: none;
}
button.mfp-close,
button.mfp-arrow {
  -webkit-appearance: none;
  -webkit-box-shadow: none;
  background: transparent;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  display: block;
  outline: none;
  overflow: visible;
  padding: 0;
  z-index: 11006;
}
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.mfp-close {
  color: #fff;
  font-family: Arial, Baskerville, monospace;
  font-size: 0;
  font-style: normal;
  height: 44px;
  line-height: 44px;
  opacity: 0.65;
  padding: 0 0 18px 10px;
  position: absolute;
  right: 0;
  text-align: center;
  text-decoration: none;
  top: 0;
  transition: opacity 0.4s;
  width: 44px;
}
.mfp-close:before {
  background-color: rgba(0, 0, 0, 0.6);
  border: 2px solid #FFFFFF;
  border-radius: 50%;
  box-shadow: 2px 2px 3px #000000;
  font-family: "megatron" !important;
  content: "\e1b8";
  cursor: pointer;
  font-size: 10px;
  height: 24px;
  line-height: 20px;
  position: absolute;
  right: 10px;
  text-align: center;
  top: 5px;
  width: 24px;
}
.mfp-close:hover,
.mfp-close:focus {
  opacity: 1;
}
.mfp-close-btn-in .mfp-close {
  color: #333;
}
.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: #fff;
  padding-right: 6px;
  right: -6px;
  text-align: right;
  width: 100%;
}
.mfp-counter {
  color: #ccc;
  font-size: 12px;
  line-height: 18px;
  position: absolute;
  right: 0;
  top: 0;
  white-space: nowrap;
}
button.mfp-arrow {
  transition: all 0.3s ease-in-out;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 46px;
  height: 46px;
  color: #FFFFFF;
  background-color: transparent;
  font-size: 0;
  overflow: hidden;
  opacity: 0.6;
}
button.mfp-arrow:hover {
  opacity: 1;
}
button.mfp-arrow:before {
  display: block;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 44px;
  line-height: 46px;
  font-family: "megatron" !important;
}
.mfp-arrow-left {
  left: 20px;
}
.mfp-arrow-left:before {
  content: "\e1ad";
  left: -2px;
}
.mfp-arrow-right {
  right: 20px;
}
.mfp-arrow-right:before {
  content: "\e1ab";
  right: -2px;
}
.mfp-iframe-holder {
  padding-bottom: 40px;
  padding-top: 40px;
}
.mfp-iframe-holder .mfp-close {
  top: -40px;
}
.mfp-iframe-holder .mfp-content {
  line-height: 0;
  max-width: 900px;
  width: 100%;
}
.mfp-iframe-scaler {
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
  width: 100%;
}
.mfp-iframe-scaler iframe {
  background: #000;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
/* Main image in popup */
img.mfp-img {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  height: auto;
  line-height: 0;
  margin: 0 auto;
  max-width: 100%;
  padding: 40px 0 40px;
  width: auto;
}
/* The shadow behind the image */
.mfp-figure {
  line-height: 0;
}
.mfp-figure:after {
  background: #444;
  bottom: 40px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  content: '';
  display: block;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 40px;
  width: auto;
  z-index: -1;
}
.mfp-figure figure {
  margin: 0;
}
.mfp-figure small {
  color: #bdbdbd;
  display: block;
  font-size: 12px;
  line-height: 14px;
}
.mfp-bottom-bar {
  cursor: auto;
  left: 0;
  margin-top: -36px;
  position: absolute;
  top: 100%;
  width: 100%;
}
.mfp-title {
  color: #f3f3f3;
  line-height: 18px;
  padding-right: 36px;
  text-align: left;
  word-wrap: break-word;
}
.mfp-image-holder .mfp-content {
